/*
 *  Filtes Banner
 *    Styles for the filter banner snippet.
 *
 *  Version:
 *    1.0.0 - 2021/11/15
 */

.c-filter-banner {
  padding: 3.5rem $s-gutter--edge-mobile;
  background-color: $s-color--background-focus;
  text-align: center;

  &__title {
    margin-bottom: 0.8125rem;
  }

  &__drawer-trigger {
    cursor: pointer;
  }

  &__description {
    max-width: 43.75rem;
    margin: 0 auto;

    a {
      @include t-font--link;
      position: relative;
      padding-bottom: 0.0625rem;
      cursor: pointer;
      color: $s-color--font-focus;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    padding: 4rem $s-gutter--edge-mobile 6rem;

    &__title {
      margin-bottom: 0.875rem;
    }

    &__drawer-trigger {
      display: none;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 4.5rem #{'min(10%, 12.5rem)'} 7.75rem;

    &__drawer-trigger {
      display: none;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 7.5rem #{'min(10%, 12.5rem)'} 12.125rem;

    &__title {
      margin-bottom: 1.25rem;
    }
  }
}