/*
 *  Collection Pagination
 *    Styles for the collection pagination snippet.
 *
 *  Version:
 *    1.0.0 - 2021/11/15
 */

.c-collection-pagination {
  padding: 0 $s-gutter--edge-mobile 4.3125rem;
  text-align: center;

  &__count {
    margin-bottom: 0;
    font-weight: $s-font--root-weight;

    &.is-hidden {
      display: none;
    }
  }

  // Image
  &__illustration {
    &-container {
      padding-bottom: 64.6%;
    }
  }

  // Text
  &__btn {
    position: relative;
    width: 10.0625rem;
    margin-top: 2.125rem;
    cursor: pointer;

    &.is-hidden {
      display: none;
    }

    &-label {
      position: absolute;
      inset: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1.75rem 2.875rem;
      z-index: $s-z--text-overlay;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    padding: 0 #{'min(6%, 7.5rem)'} 3.825rem;

    &__btn {
      width: 11rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    max-width: $s-page-wrap--xlarge;
    padding: 0 #{'min(6%, 7.5rem)'} 6.375rem;
    margin: 0 auto;

    &__btn {
      width: 13.4375rem;

      &-label {
        padding: 2.75rem 3.5938rem 2rem;
      }
    }
  }
}
