/*
 *  Content Block
 *    Styles for the content block object.
 *
 *  Version:
 *    1.0.0 - 2021/11/15
 */

.o-content-block {
  display: none;
  padding-bottom: 5rem;

  &__inner {
    position: relative;
  }

  &.is-primary {
    padding-right: 4.3125rem;

    .o-content-block {
      &__content-container {
        right: -4.3125rem;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 13.125rem;
        height: 9.625rem;
        padding: 2rem 2.625rem 2rem 1.875rem;
        text-align: center;
        background: url(../../assets/svg.shape-17.svg) no-repeat center center/100%;
      }

      &__illustration {
        transform: scaleX(-1);
        &-wrapper {
          position: absolute;
          top: -2.9375rem;
          right: -0.25rem;
          width: 5.25rem;
          height: 4.5rem;
        }
      }
    }
  }

  &.is-secondary {
    padding-left: 4.5625rem;

    .o-content-block {
      &__content-container {
        left: -4.5625rem;
        width: 13.4375rem;
        height: 8.125rem;
        padding: 1.9375rem 2.1875rem 2rem;
        text-align: center;
        background: url(../../assets/svg.shape-18.svg) no-repeat center center/100%;
      }
    }
  }

  &.is-visible {
    display: block;
  }

  &__content {
    margin-bottom: 0;
    color: $s-color--font-invert;

    &-container {
      position: absolute;
      bottom: -5rem;
    }
  }

  &__image-container {
    padding-bottom: 129.43%;
  }

  /*** Media Queries ***/

  @include t-media-query($s-xlarge-down) {
    &__content {
      font-size: 0.9375rem;
      line-height: 1.46;
    }
  }

  @include t-media-query($s-medium-up) {
    &.is-secondary {
      padding-right: 4.5625rem;
      padding-left: 0;

      .o-content-block {
        &__content-container {
          right: -4.5625rem;
          left: initial;
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding-bottom: 5.625rem;

    &.is-primary {
      padding-right: min(17.23%, 9.25rem);

      .o-content-block {
        &__content-container {
          bottom: -5.625rem;
          right: -9.25rem;
          width: 30.0625rem;
          height: 22.125rem;
          padding: 6.25rem 3.9375rem 6.25rem 2.4375rem;
        }

        &__illustration {
          &-wrapper {
            top: -2.25rem;
            right: -0.75rem;
            width: 10rem;
            height: 8.6875rem;
          }
        }
      }
    }

    &.is-secondary {
      padding-right: min(17.23%, 9.25rem);

      .o-content-block {
        &__content-container {
          right: min(-17.23%, -9.25rem);
          width: 28.4375rem;
          height: 18rem;
          padding: 4.25rem 2.3125rem 4rem 2.4375rem;
        }
      }
    }
  }
}