/*
 *  Seo Content
 *    Styles for the seo content snippet.
 *
 *  Version:
 *    1.0.0 - 2021/11/18
 */

.c-seo-content {
  padding: 0 1.875rem 5.125rem;
  text-align: center;

  p {
    @include t-font--paragraph-2;
    margin-bottom: 1.75rem;
  }

  h1, h2, h3, h4, h5, h6 {
    @include t-font--heading-5;
    margin-bottom: 1.75rem;
    font-weight: $s-font--root-weight;
  }

  // Content
  &__content {
    max-width: 31.25rem;
    margin: 0 auto;

    .is-mobile {
      display: block;
    }

    .is-desktop {
      display: none;
    }

    &.is-full {
      display: none;
    }
  }

  &.is-open {
    .c-seo-content__content {
      &.is-less {
        display: none;
      }
      &.is-full {
        display: block;
      }
    }
  }

  // Button
  &__btn {
    &.is-more {
      display: inline;
    }

    &.is-less {
      display: none;
    }
  }

  &.is-open {
    .c-seo-content__btn {
      &.is-more {
        display: none;
      }

      &.is-less {
        display: inline;
      }
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    h1, h2, h3, h4, h5, h6 {
      @include t-font--paragraph-1-laptop;
      margin-bottom: 1.25rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    h1, h2, h3, h4, h5, h6 {
      @include t-font--paragraph-1-desktop;
    }

    &__content {
      max-width: 52rem;

      .is-mobile {
        display: none;
      }

      .is-desktop {
        display: block;
      }
    }
  }
}
