/*
 *  Products Loop
 *    Styles for the products loop snippet.
 *
 *  Version:
 *    1.0.0 - 2021/11/15
 */

.c-collection {
  &__loop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4.0625rem 1rem;
    padding: 1.875rem $s-gutter--edge-mobile 3.5625rem;
  }

  &__content-block {
    grid-column: span 2;
    grid-row: span 2;
  }

  &__no-results {
    display: none;
    margin-bottom: 0;
    grid-column: span 2;
    text-align: center;

    &.is-visible { display: block; }
  }

  &__filter-banner.is-filters-hidden{
    padding-bottom: 1.5rem;
    .c-filter-banner__filters,
    .c-filter-banner__drawer-trigger{
      display: none;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__loop {
      grid-template-columns: repeat(3, 1fr);
    }

    &__no-results {
      grid-column: span 3;
    }
  }

  @include t-media-query($s-medium-up) {
    &__loop {
      padding: 2.5625rem #{'min(6%, 7.5rem)'};
      grid-template-columns: repeat(4, 1fr);
      gap: 4.5rem 1.5rem;
    }

    &__no-results {
      grid-column: span 4;
    }

    &__filter-banner.is-filters-hidden{
      padding-bottom: 4.5rem;    
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__loop {
      max-width: $s-page-wrap--xlarge;
      padding: 4.3125rem #{'min(6%, 7.5rem)'};
      margin: 0 auto;
      gap: 6.875rem 2.625rem;
    }
  }
}
