/*
 *  Faceted Nav
 *    Styles for the faceted nav snippet.
 *
 *  Version:
 *    1.0.0 - 2021/11/15
 */

.c-collection-faceted-nav {
  $block: #{&};
  @include t-anim--transition(left, $ease: linear);
  position: fixed;
  top: 2.5rem;
  left: -100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100vh - 2.5rem);
  text-align: left;
  background-color: $s-color--background-subdued;
  z-index: $s-z--filter-drawer;

  &.is-open { left: 0; }

  .is-desktop { display: none; }

  &__drawer-trigger {
    width: 4rem;
    padding: 1.1563rem 1.25rem 1rem;
  }

  &__body {
    position: relative;
    flex: 1;
    padding: 0 $s-gutter--edge-mobile;
    overflow: hidden;

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 2.5rem;
      background: linear-gradient(to bottom, rgba($s-color--background-subdued, 0), rgba($s-color--background-subdued, 1));
      pointer-events: none;
      content: "";
    }
  }

  &__container {
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none;
    height: 100%;

    &::-webkit-scrollbar {
      width: 0px;
      background: transparent;
    }
  }

  &__footer {
    padding: 2.5rem $s-gutter--edge-mobile;
    text-align: center;
  }

  &__title {
    position: relative;
    padding: 1rem 0;
    cursor: pointer;
    user-select: none;
    list-style: none;
    font-weight: $s-font--root-weight;

    &-prefix {
      display: inline-flex;
    }

    .is-sort & {
      padding: 1.75rem 0 1.5rem;
    }

    &::-webkit-details-marker {
      display: none;
    }

    &::after {
      @include t-anim--transition(transform, $s-anim--time-short, linear);
      @include t-absolute-center-y;
      right: 0;
      display: block;
      width: 1rem;
      height: 1rem;
      background: url(../../assets/icon-caret-down.svg) no-repeat center center;
      content: "";
      transform: translateY(-50%) rotate(-180deg);

      .is-closed & {
        transform: translateY(-50%);
      }
    }
  }

  &__group {
    &:first-child {
      border-bottom: 0.0625rem solid $s-color--border-subdued-20;
      margin-bottom: 1rem;
    }
  }

  &__check {
    &-group {
      padding: 1rem 0.375rem 2.5rem;
      margin: 0;
      list-style: none;

      .is-sort & {
        padding-top: 0;
        padding-bottom: 0;
      }

      .is-color & {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 1.25rem 9.1325%;
      }
    }

    &-body {
      .is-closed & {
        display: none;
      }
    }

    &-item {
      position: relative;
      padding: 0.75rem 0;
      cursor: pointer;

      &:first-child {
        padding-top: 0;
      }

      &:last-child {
        padding-bottom: 1.5rem;
        margin-bottom: 0;
      }

      &-inner.is-selected {
        position: relative;
        padding-left: 1rem;

        &::after {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          display: block;
          width: 0.5rem;
          height: 0.5rem;
          background: url(../../assets/svg.circle-blob.svg) no-repeat center center;
          content: "";
        }
      }
    }
  }

  &__btn {
    position: relative;
    width: 100%;
    margin-top: 0.25rem;
    cursor: pointer;

    &-svg-outline {
      position: absolute;
      top: -0.25rem;
      left: -0.3125rem;
    }

    &-svg, &-svg-outline {
      width: 100%;
      height: 100%;
    }
  }

  .o-checkbox__label {
    display: block;
    cursor: pointer;
  }

  .o-checkbox__checkbox:checked + .o-checkbox__label {
    position: relative;
    padding-left: 1rem;

    &::after {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      display: block;
      width: 0.5rem;
      height: 0.5rem;
      background: url(../../assets/svg.circle-blob.svg) no-repeat center center;
      content: "";
    }
  }


  &__check-group.is-boxes {
    #{$block}__check-item {
      text-transform: capitalize;
      .o-checkbox__label {
        position: relative;
        padding-left: 1.8125rem;
      }
      .o-checkbox__label::after {
        content: '';
        position: absolute;
        bottom: 50%;
        left: -0.375rem;
        transform: translateY(50%);
        width: 0.875rem;
        height: 0.875rem;
        border: 2px solid $s-color--border-focus;
        border-radius: 0.1875rem;
      }
      .o-checkbox__checkbox:checked + .o-checkbox__label::after {
        bottom: 50%;
        top: auto;
        background: url(../../assets/icon-check.svg) no-repeat center center;
        background-size: 60% auto;
        background-color: $s-color--border-focus;
      }

      .o-checkbox:hover .o-checkbox__label::after {
        opacity: 0.5;
      }
    }
  }

  &__reset {
    display: block;
    cursor: pointer;
    color: $s-color--font-focus;
    margin-top: -2.75rem;
    margin-bottom: 2rem;
    &.is-color {
      margin-top: -1.5rem;
      margin-bottom: 12px;
    }
  }

  &__reset-inner {
    padding-bottom: 0.2em;
    border-bottom: 1px solid $s-color--font-focus;
  }

  /*** Media Queries ***/

  @media only screen and (min-width: 34.375rem) {
    .is-color  &__check-group {
      grid-template-columns: repeat(7, 1fr);
      gap: 1.25rem 6.76%;
    }
  }

  @include t-media-query($s-small-up) {
    // Override inline styles from js script
    top: initial!important;
    height: auto!important;

    position: initial;
    left: initial;
    display: block;
    width: auto;
    background-color: transparent;
    transition: none;
    z-index: initial;

    &__header, &__footer {
      display: none;
    }

    &__body {
      padding: 0;
      overflow: initial;
      text-align: center;

      &::after {
        display: none;
      }
    }

    &__container {
      display: inline-flex;
      gap: 4rem;
      justify-content: center;
      justify-items: center;
      margin: 0 auto;
      overflow: initial;
      height: auto;
    }

    &__group {
      position: relative;

      &:first-child {
        border-bottom: 0;
        margin-bottom: 0;
      }

      &.is-sort {
        order: 2;
      }

      &.is-availability {
        order: 1;
      }

      &.is-color {
        order: 0;
        position: initial;
      }
    }

    &__title, .is-sort &__title {
      padding: 0.75rem 1.5rem 0.75rem 0;
    }

    &__title {
      &-prefix {
        display: inline;
      }

      &-seperator {
        display: none;
      }

      &-value {
        display: none;
      }
    }

    &__check {
      &-body {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        min-width: 12.5rem;
        border-radius: 0.5rem;
        text-align: left;
        background-color: $s-color--background-subdued;
        z-index: $s-z--text-overlay;

        .is-color & {
          background-color: transparent;
        }
      }

      &-group {
        padding: 0;

        .is-color & {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          gap: 1rem;
        }
      }

      &-item {
        padding: 0.75rem 1.5rem;

        &:first-child {
          padding-top: 1rem;
        }

        &:last-child {
          padding-bottom: 1rem;
        }
      }
    }

    &__btn {
      width: 2rem;
      height: 2rem;
      margin-top: 0;
    }
    &__check-group.is-boxes {
      max-height: 14.0625rem;
      overflow: auto;
      #{$block}__check-item {
        .o-checkbox__label {
          padding-left: 1.8125rem;
        }
        .o-checkbox__label::after {
          left: 0;
        }
      }
    }

    &__reset {
      margin-top: 0;
      margin-bottom: 0;
      text-align: center;
      padding: 0.875rem;
      position: relative;
      width: 100%;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0.875rem;
        right: 0.875rem;
        width: auto;
        height: 1px;
        background-color: $s-color--border-subdued;
        opacity: 0.2;
      }
      &.is-color {
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 1.625rem;
        &::before {
          content: normal;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    .is-color &__check-group {
      padding-top: 1rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__container {
      gap: 5.5rem;
    }

    &__title {
      padding-right: 2.5rem;

      .is-sort & {
        padding-right: 2.5rem;
      }

      &::after {
        width: 1.5rem;
        height: 1.5rem;
      }
    }

    .is-color &__check-group {
      padding-top: 2.25rem;
      justify-content: flex-start;
      gap: 2.5rem;
    }

    &__btn {
      width: 2.5625rem;
      height: 2.5rem;

      &-svg-outline {
        left: -0.3125rem;
        width: 2.6875rem;
        height: 2.5625rem;
      }
    }
  }
}
